import { Field, ObjectType } from "@nestjs/graphql";
import { Column, Entity, ManyToOne, Relation } from "typeorm";
import { BaseEntityColumn } from "../base.entity";
import { PaymentGatewayKey } from "./payment-gateway-key.entity";

@ObjectType()
@Entity()
export class Paypal extends BaseEntityColumn {

  @Field(() => String)
  @Column()
  orderId: string;

  @Field(() => String)
  @Column()
  name: string;

  @Field(() => String)
  @Column()
  returnUrl: string;

  @Field(() => String)
  @Column()
  cancelUrl: string;

  @Field(() => String)
  @Column()
  paymentType: string;

  @Field(() => String)
  @Column()
  paymentMode: string;

  @Field(() => PaymentGatewayKey)
  @ManyToOne(() => PaymentGatewayKey, (paymentGatewayKey) => paymentGatewayKey.paypals)
  paymentGatewayKey: Relation<PaymentGatewayKey>;

  @Field(() => String)
  @Column('simple-json', { nullable: true })
  orderJson: {};

}
